import React , {useEffect, useState} from 'react';
import { navigate } from 'gatsby';

import{ PersonSVGIcon } from '@react-md/material-icons';
import {  MdHome } from '@react-icons/all-files/md/MdHome';
import {  MdAccountBox } from '@react-icons/all-files/md/MdAccountBox';
import { RiPercentFill } from '@react-icons/all-files/ri/RiPercentFill';
import { HiArchive } from '@react-icons/all-files/hi/HiArchive';
import {  MdCheck } from '@react-icons/all-files/md/MdCheck';
import { GiHamburgerMenu} from '@react-icons/all-files/gi/GiHamburgerMenu';
import { MdStore } from '@react-icons/all-files/md/MdStore'
import logo from '../images/watsbulkAsset 7.svg';

import Button from 'react-bootstrap/Button';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Tooltip from 'react-bootstrap/Tooltip';
import account from '../images/icons8-account-32.png';

import './sidebar_links_animation.css'
import { verify } from '../api_handlers/account_api';
import { isBrowser } from '../utils/isBrowser';

const sidebar = {
	position: 'fixed',
    background: 'rgba(0,103,86,255)',
    boxShadow: '0 8px 32px 0 rgba(0,103,86,255)',
    backdropFilter: 'blur( 16.5px )',
    width: '11rem',
    height: 'calc(100vh)',
    padding: '0px 20px',
    display: 'flex',
    flexFlow: 'column',
    alignItems: 'stretch',
}

const collapsed_sidebar = {
	...sidebar,
	width:'6rem'
}
const logoContainer = {
	display:'flex',
	alignItems:"center",
	flexFlow:'column',
	height:'7rem',
	marginTop:'2rem',
	color:'#fff'
}

const logo_ = {
	width:'2.5rem',
	margin:'0 5px'
}
const linkWrapper = {
	display:'flex',
	flexFlow:'column',
	alignItems:'stretch',
	alignSelf:'flex-end',
	width:'10rem',
	padding:15
}
const collapsedLinkWrapper = {
	display:'flex',
	flexFlow:'column',
	alignItems:'center',
	alignSelf:'center',
	width:'10rem',
	padding:15,
	maxWidth:'100%'
}

const imageWraper = {
	display:'flex',
	justifyContent:'center',
	alignItems:'center',
	width:'8rem',
	height:'8rem'
}

const image = {
	padding:'1rem',
	border:'1px solid  rgb(42 38 42 / 81%)',
	borderRadius:'50%',
	width:'7rem',
	height:'7rem'
}
const accountWrapper = {
	display:'flex',
	flexFlow:'column',
	justifyContent:'space-between',
	margin:'1rem 0'
}
const shopName = {

}

const City = {
	color:'#eee',
	textAlign:'center',
	fontWeight:550,
	fontSize:'.85rem'
}

const link={
    textRendering: "optimizeLegibility",
    margin: '10px 0',
	cursor:'pointer'
};

const SelectedLink = {
	border:" 2px solid #fff",
    textRendering: "optimizeLegibility",
    borderRadius: 10,
    margin: '10px 0',
	
	width:'3rem',
	padding:'0 3px',
	background:'#eee',
    color:'#222'
}
const collapsed_selected_link = {
    textRendering: "optimizeLegibility",
    margin: '10px 0',
    borderLeft:'2px solid #eee',
    color:'#222'
}
const text = {
	color:'#eee',
	textAlign:'center',
	fontWeight:670
}
const link_p={
	...text,
	margin:"10px 0",
}
const link_black = {
	...link_p,
	color:'#222'
}

const logout_show = {
	transition:'.1s 1 ease-in 1',
	marginTop:'2rem',
	marginBottom:'3rem'
}
const logout_hide={display:'none'}

// <OverlayTrigger
// 			placement="right"
// 			overlay={<Tooltip id="button-tooltip-2">Coupons</Tooltip>}
// 			>
			
// 			<div id="links" 
// 			style={selected=='coupons'?collapsed?collapsed_selected_link : SelectedLink:link} >
// 			<p style={selected=='coupons'?link_black:link_p} onClick={()=>navigate('/coupons')}><RiPercentFill style={{margin:'0 5px 3px 0'}}/>{collapsed?`Coupons`:''}</p>
// 			<div id="underline"></div>
// 			</div>
			
// 			</OverlayTrigger>

export function Sidebar({selected , collapsed , setCollapsed}){
	if(!isBrowser){return}
	const [selection , setSelection] = useState(selected);
	const [sellerData , setSellerData] = useState({shop_name:'',city:'',profile_picture:''})
	let [action , setAction]= useState({text:'',action:''})
	useEffect(()=>{

		setCollapsed(false)
	  setAction({text:'Login',action:()=>{
		localStorage.removeItem('CSRF_token');
		navigate('/login')}
	  })
	  let store_data = localStorage.getItem('store_')
	  if(store_data && typeof store_data === 'object' ){
	  setSellerData({...JSON.parse(store_data)});
	}
	},[])
	useEffect(()=>{
	if(isBrowser){
		let token = localStorage.getItem('CSRF_token')
  if(token === 'undefined' || token === 'null' || !token){
      navigate('/login')
  }
  if(token){
	  if(token){
		verify()
		.then(isMatch=>{
		if(isMatch){
		  if(isMatch.success){
			let store_ = JSON.parse(localStorage['store_']);
			// store_.email = isMatch.email;
			// store_.number = isMatch.number;
			// store_.name = ismatch.name;
			// store_.currency = isMatch.currency;
			localStorage.setItem('store_',JSON.stringify({...store_,...isMatch}));
			setAction(i=>{
			  return {
				text : 'Logout',
				action:()=>{
				localStorage.removeItem('CSRF_token');
				navigate('/login')}
			  } });
			if(window.location.href.includes('login') || window.location.href.includes('signup')){
			//   navigate('/products');
			console.log('navigating to products')
			}
		  }
		  else{
			navigate('/login')
		  }
		}
		  else{
			navigate('/login')
		  }
	
		})
		.catch(e=>{
		  console.log(e.data)
		})
	}

}}
	},[])	

	return (
	<div style={collapsed?sidebar : collapsed_sidebar}>
	<div style={logoContainer}>
	<div style={{display:'flex',alignItems:'flex-end'}}>
	<div className="menu_button" style={{alignSelf:'center',padding:7,borderRadius:10}}>
	<GiHamburgerMenu style={{width:'1.5rem',height:'1.5rem'}} onClick={()=>{
		setCollapsed(!collapsed)
	}}></GiHamburgerMenu>
	</div>
	<img src={logo} style={logo_}/>
	<span>{collapsed?"Redcat" : null}</span>
	</div>

	
	 </div>
	 {collapsed ? 
		<div style={accountWrapper}>
		<div style={imageWraper}>
		<MdStore style={image}/>
		</div>
		<p style={text}>{sellerData.shop_name}</p>
		<p style={City}>{sellerData.city}</p>
		</div>
		:null
	 }
		<div style={collapsed?linkWrapper:collapsedLinkWrapper}>
		<OverlayTrigger
		placement="right"
		overlay={<Tooltip id="button-tooltip-2">Dashboard</Tooltip>}
		>
		<div id="links" 
		style={selected=='home'? collapsed?collapsed_selected_link : SelectedLink:link} >
		<p style={selected=='home'?link_black:link_p} onClick={()=>navigate('/dashboard')}><MdHome style={{margin:'0 5px 3px 0'}}/>{collapsed?`Home`:''}</p>
		<div id="underline"></div>
		</div>
		</OverlayTrigger>

		<OverlayTrigger
		placement="right"
		overlay={<Tooltip id="button-tooltip-2">Products</Tooltip>}
		>
		
			<div id="links" 
			style={selected=='products'?collapsed?collapsed_selected_link : SelectedLink:link} >
			<p style={selected=='products'?link_black:link_p} onClick={()=>navigate('/product/all')}><HiArchive style={{margin:'0 5px 3px 0'}}/>{collapsed?`Products`:''}</p>
			<div id="underline"></div>
			</div>
			
			</OverlayTrigger>

			<OverlayTrigger
			placement="right"
			overlay={<Tooltip id="button-tooltip-2">Orders</Tooltip>}
			>
			
			<div id="links" 
			style={selected=='orders'?collapsed?collapsed_selected_link : SelectedLink:link} >
			<p style={selected=='orders'?link_black:link_p} onClick={()=>navigate('/orders')}><MdCheck style={{margin:'0 5px 3px 0'}}/>{collapsed?`Orders`:''}</p>
			<div id="underline"></div>
			</div>
			
			</OverlayTrigger>
			
		<OverlayTrigger
		placement="right"
		overlay={<Tooltip id="button-tooltip-2">Account</Tooltip>}
		>
		
			<div id="links" 
			style={selected=='account'?collapsed?collapsed_selected_link : SelectedLink:link} >
			<p style={selected=='account'?link_black:link_p} onClick={()=>navigate('/account')}><MdAccountBox style={{margin:'0 5px 3px 0'}}/>{collapsed?`Account`:''}</p>
			<div id="underline"></div>
			</div>
			
			</OverlayTrigger>

			
		</div>
		
			
			<Button variant="outline-light"  style={collapsed?logout_show:logout_hide} onClick={action.action}>{action.text}</Button>
			
	</div>
	)
}
